<template>
  <transition name="fade">
    <div class="dialog-bg" v-if="visible"></div>
  </transition>
  <transition name="slide-fade">
    <div class="dialog-wrapper" @click="hideModal" v-if="visible">
      <div @click.stop class="dialog__content" v-if="visible">
        <div :class="['header', modalColors ]" >{{ header }}</div>
        <div className='modal_flex'>
          <img v-if="picture" :src=" define.pathToGraphics + picture " alt=''>
          <div class="message" v-html="message"></div>
        </div>
        <div class="btns">
          <button :class="['btn2', btn1Color ]" @click="confirmClick" >{{ btn1Text }}</button>
          <button :class="['btn2', btn2Color ]" @click="hideModal" >{{ btn2Text }}</button>
        </div>
      </div>
    </div>
  </transition>
</template>

<script>
import { define } from '@/data/globals.js'

export default {
  props: { 
    header: { type: String, default: 'Модальное окно' },
    message: { type: String, default: 'Текст модального окна' },
    btn1Text: { type: String, default: 'Ок' },
    btn2Text: { type: String, default: 'Отмена' },
    visible: {
        type: Boolean,
        default: false
    },
    modalColors: { type: String, default: '' },
    pic: { type: String, default: '' },
  },
  data() {
    return {
      picture: '',
      btn1Color: 'blueBtn',
      btn2Color: 'blueBtn',
    }
  },
  emits: ['update:visible', 'confirmed'],
  methods: {
    hideModal() {
        this.$emit('update:visible', false)
    },
    confirmClick() {
        this.$emit('confirmed')
    },
    setModalStyle() {
      switch(this.pic) {
        case 'alert':
          this.picture = '/alert_48px.png'
          this.btn1Color = 'orangeBtn'
          this.btn2Color = 'orangeBtn'
          break
        case 'info':
          this.picture = '/info_48px.png'
          this.btn1Color = 'blueBtn'
          this.btn2Color = 'blueBtn'
          break
        default:
          this.picture = ''
          this.btn1Color = 'greenBtn'
          this.btn2Color = 'greenBtn'
          break
      }// switch
    },
  },
  mounted() {
    this.setModalStyle()
  },
  // Composition API
  setup() {
      return { define }
  }
}
</script>

<style scoped>
.dialog-bg {
  z-index: 102;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.4);
  position: fixed;
}

.dialog-wrapper {
  z-index: 103;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  position: fixed;
  display: flex;
}

.dialog__content {
  max-width: 520px;
  /* width: 100%; */

  margin: 20vh auto auto auto;
  background: white;
  border-radius: 10px;
  box-shadow: 2px 2px 20px  #555555;
}

@media (min-width: 360px) {
  .dialog__content {
    min-width: 360px;
  }
}

.modalBlue {
    border-top: 5px solid #00a6f3;
}
.modalOrange {
    border-top: 5px solid orange;
}

.header {
  padding: 6px 12px;
  background: #e4e4e4;
  border-radius: 10px 10px 0 0;
  /* font-weight: bold; */
}

.modal_flex {
    display: flex;
    align-items: center;
    min-height: 90px;
}

.modal_flex img {
    margin-left: 20px;
}

.message {
  padding: 20px;
  font-size: 1.3rem;
}

.btns {
  padding: 11px 0 0 15px;
  display: flex;
  justify-content: flex-end;
  background: #e4e4e4;
  border-radius: 0 0 10px 10px;
}

button {
  margin-right: 15px;
  margin-bottom: 11px;
}

.fade-enter-active, .fade-leave-active { transition: opacity .2s ease; }

.fade-enter-from, .fade-leave-to { opacity: 0; }


.slide-fade-enter-active { transition: all .2s ease-out; }

.slide-fade-leave-active { transition: all .2s cubic-bezier(1, 0.5, 0.8, 1); }

.slide-fade-enter-from {
  transform: translateY(-40px);
  opacity: 0;
}
.slide-fade-leave-to {
  transform: translateY(40px);
  opacity: 0;
}









</style>