<template>
        <!-- visible={delModal}
        modalColors="modalOrange"
        header="Удаление"
        pic='alert'
        message={modalProdName}
        btn1Text="Да"
        btn2Text="Нет"
        сonfirmed={deleteProduct}
        hide={hideModal} -->
    <!-- v-model:show="delModal" -->
    <my-modal
        v-model:visible="delModalVisible"
        :header="insert[lang].myModal.header"
        :message="modalText"
        :btn1Text="insert[lang].myModal.btn1Text"
        :btn2Text="insert[lang].myModal.btn2Text"
        @confirmed="deleteProduct"
        
        modalColors="modalOrange"
        pic='alert'
    />

    <!-- {{ $store.state.updProd.id }} / {{ $store.state.updProdFreeze }} / {{ $store.state.isProdsLoading }}<br> -->

    <div v-if="$store.state.updProdFreeze" class="loader1"></div>
    <!-- <div v-if="$store.state.isProdsLoading" class="loader1"></div> -->
    <!-- <div v-if="$store.state.isProdsLoading || $store.state.updProdFreeze" class="loader1"></div> -->
    <div v-else>
        <div v-if="!$store.state.updProd.brand || !$store.state.updProd.model">
            ID: <b>{{ $route.params.id }}</b><br>
            <div v-html="insert[lang].empty"></div>
        </div>
        <div v-else>
        <!-- <div v-if="item.id"> -->
        <!-- <div v-if="item.id > 0"> -->
        <!-- <div v-if="item.id"> -->
            <button class="btn btn-success mr-6 min-80"
                @click.stop="$router.push('/edit/'+item.id)"
                >{{ insert[lang].editBtn }}</button>
            <button class="btn btn-warning min-80"
                @click.stop="confirmDelete(item.id, item.model)"
                >{{ insert[lang].deleteBtn }}</button>
            <hr>
            <h1>{{ item.brand }}</h1>
            <h2>{{ item.model }}</h2>
            <p>{{ insert[lang].price }}: {{ item.price }} USD</p>

            <div v-if="item.pictures">
                <span v-for="(pic, index) in item.pictures" :key="pic">
                    <div v-if="index === 0" class="mb-20">
                        <img :src=" define.pathToUploads + '/' + pic " class="img-fluid" alt="">
                    </div>
                    <img v-else :src=" define.pathToUploads + '/40px/' + pic " class="img-fluid mr-15 " alt="">
                </span>
            </div>
            <img v-else 
                :src=" define.pathToGraphics + '/default2_190px.png' " alt="">
        
            <!-- <img v-if="item.pictures[0]"
                :src=" define.pathToUploads + '/' + item.pictures[0] " class="img-fluid" alt="">
            <img v-else
                :src=" define.pathToGraphics + '/default2_190px.png' " alt=""> -->
        </div>
        <!-- <div v-else>
            ID: <b>{{ $route.params.id }}</b><br>
            <div v-html="insert[lang].empty"></div>
        </div> -->
    </div>
        <!-- <div v-if="item.pictures">
            <div v-for="(value, index) in item.pictures"
                :key="value"
            >
            <img v-if="index === 0"
                :src=" define.pathToUploads + '/' + value " class="img-fluid" alt="">
            </div>
        </div>
        <img v-else 
            :src=" define.pathToGraphics + '/default2_190px.png' "
            alt="">
        </div>
        <div v-else>
            ID: {{ $route.params.id }}<br>
            <div v-html="insert[lang].empty"></div>
        </div> -->
</template>

<script>
import { define } from '@/data/globals.js'
import useAuth from '@/hooks/useAuth'
import MyModal from '@/components/UI/MyModal'
import axios from 'axios'
import useProducts from '@/hooks/useProducts'
import insert from '@/data/languages/ProdInfoViewData.js'
import useNotify from '@/hooks/useNotify'
import notifys from '@/data/languages/notifications/ProdInfoData'
import useLang from '@/hooks/useLang'

export default {
    components: {
        MyModal,
    },
    // props: {
    //     // item: {
    //     //     type: Object,
    //     //     required: true
    //     // },
    // },
    data() {
        return {
            name: 'ProdInfo',
            // languages: languages,
            // insert: languages.en,

            item: '',
            idToDelete: 0,
            modalText: '',
            delModalVisible: false,
        }
    },
    // emits: ["create"],
    methods: {
        getItem() {
        // const filteredArr = this.$store.state.updProd.pictures.filter(item => item !== filename)
        // const {id, name, description} = this.products.find(item => item.id === this.$store.state.updProd.id)
        // console.log('1) this.item: ', this.item);
        // console.log('this.$route.params.id: ', this.$route.params.id);
        this.item = this.$store.state.products.find(item2 => item2.id == this.$route.params.id)

        
        // console.log('this.$store.state.products: ', this.$store.state.products);
        // console.log('2) this.item: ', this.item);
        // console.log('this.$route.params.id: ', this.$route.params.id);
        },

        confirmDelete(id, name){
            // console.log('store products.pictures: ', this.$store.state.products[0].pictures[0]);

            this.idToDelete = id
            this.modalText = this.insert[this.lang].myModal.question + ' <b>' + name + '</b>?' // Удалить <b>...</b> ?
            this.delModalVisible = true
            console.log('-> confirmDelete() id:' + id)
        },

        // deleteProduct() {
        async deleteProduct() {
            console.log('\n\n@click ~~~~~~~~~~ deleteProduct ~~~~~~~~~~ ')
            // console.log('Создание товара. Перекрыть экран ("заблокировать интерфейс") и показать спинер.\nОтправить запрос на бэк');

            const customAxios = axios.create({ // baseURL: 'http://yourcoolapi.com/api',
                withCredentials: true, 
            })

            customAxios.interceptors.request.use(
                config => {
                    console.log('>>>> customAxios - request (config)')

                    let localToken = localStorage.getItem('AccessToken')
                    if(localToken) localToken = encodeURI(localToken)
                    // Для информации:
                    // encodeURI заменяет киррилические и другие символы на соответствующими им UTF-8 escape-последовательности. Незаменяемые символы: латинский алфавит, десятичные цифры, - _ . ! ~ * ' ( ) #
                    config.headers.Authorization = 'Bearer ' + localToken
                    return config
                }
            )

            customAxios.interceptors.response.use(
                config => {
                    console.log('>>>> customAxios - response (config)')
                    return config;
                },
                async error => {
                    console.log('>>>> customAxios - response (error)')
                    const originalRequest = error.config
                    if (error.response.status == 401 &&
                        error.config &&
                        !error.config._isRetry
                    ) {
                        originalRequest._isRetry = true
                        console.log('> 401 !!!!!!!!!!!: ')
                        
                        // try {
                        //     // await this.tokenRefresh()
                        //     const response = await axios.post( define.pathToAPI + "/access/refresh_token.php", 
                        //         JSON.stringify({
                        //             message: 'Привет сервер! С этим сообщением ты сможешь прочитать cookie',
                        //         }),
                        //         {
                        //             withCredentials: true, 
                        //             headers: {
                        //                 // Authorization: 'Bearer ' + encodeURI(localStorage.getItem('AccessToken')),
                        //                 Accept: 'application/json',
                        //                 'Content-Type': 'application/json',
                        //             }
                        //         }
                        //     ) // axios
                        //     console.log(' > try ')

                        //     localStorage.setItem('AccessToken', response.data.accessToken)
                        //     // customAxios.defaults.headers.common['Authorization'] = 'Bearer ' + response.data.accessToken
                        //     // axios.defaults.headers.common['Authorization'] = 'Bearer ' + response.data.accessToken
                        //     // this.$store.commit('SetIsAuth', true)
                        //     // await this.tokenRefresh()

                        //     console.log(' > try continued ')
                        //     return customAxios.request(originalRequest) 
                        // } catch(e) {
                        //     console.log(' > catch ')
                        //     console.log('НЕ АВТОРИЗОВАН')
                        // }

                        if(await this.tokenRefresh()) {
                            console.log(' tokenRefresh TRUE!')
                            return customAxios.request(originalRequest)
                        }
                        // else {
                        //     console.log(' tokenRefresh FALSE! ')
                        //     console.log('НЕ АВТОРИЗОВАН')
                        // } // if

                    }
                    throw error; // прокидывает ошибки (как бы на верхний уровень), которые вернула функция tokenRefresh()
                } // async error
            ) // customAxios.interceptors.response
                
            try {
                const response = await customAxios.post( define.pathToAPI + '/products/delete.php',
                // const { data } = await customAxios.post( define.pathToAPI + '/products/create.php',
                // const response = await axios.post( define.pathToAPI + '/products/create.php',
                    JSON.stringify({
                        // id: this.$store.state.newProd.id,
                        id: this.idToDelete
                    }),
                    {
                        // withCredentials: true, // определяет, должны ли создаваться кросс-доменные Access-Control запросы с использованием таких идентификационных данных как cookie, авторизационные заголовки или TLS сертификаты. Если здесь установили true, то на стороне бека нужно установить header("Access-Control-Allow-Credentials: true")
                        headers: {
                            // Authorization: 'Bearer YOUR_JWT_TOKEN_HERE',
                            Accept: 'application/json',
                            'Content-Type': 'application/json',
                        }
                    }
                )

                // console.log('response: ', response);
                // console.log('data: ', data);
                console.log('[3] response: ', response)
                // console.log('response.data: ', response.data);
                // console.log('response.data.status: ', response.data.status);

                // обработка ответа
                if(response.data.status === 'success') {
                    // console.log('[Получили ответ "ОК, CREATED" -> \nФиксируем изменения во VUE/Пушим зеленое уведомление]')
            // this.deleteData(this.idToDelete)

                    console.log('deleteData() id:' + this.idToDelete)
                    // Удаление объекта (по id) из массива products
            // const filteredArr = this.$store.state.updProd.pictures.filter(item => item !== filename)
            // this.item = this.$store.state.products.find(item2 => item2.id == this.$route.params.id)
                    const filteredArr = this.$store.state.products.filter(value => value.id !== this.idToDelete);
                    // this.products = this.products.filter(value => value.id !== this.$store.state.idToDelete);
                    this.$store.commit('SetProducts', filteredArr)
                    // Удаление выполнено
                    this.pushNotify( notifys[this.lang].deleted, "notifyOrange")
// ################################################################
// Проверить:
// Нужно, чтобы удалялись и те картинки которые загружены в updateform
// ################################################################
                    this.delModalVisible = false
                    this.$router.push('/')

                } else {
                    // Ошибка. Ответ сервера: ...<br>Подробности в консоли
                    this.pushNotify( notifys[this.lang].text5_1 + response.data.error + notifys[this.lang].text5_2, 'notifyOrange')
                    console.log('Ошибка:' + response)
                }

            } catch (e) {
                console.log('e: ', e);
                
                if(e.code === 'ERR_NETWORK') {
                    // Нет подключения к серверу
                    this.pushNotify( notifys[this.lang].errNetwork, 'notifyOrange')
                    console.log('Нет подключения к серверу')
                    return
                }

// Обработку ошибок желательно еще проверить
// ~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~
                // Обработка ошибок, которые вернула функция tokenRefresh()
                // (из customAxios.interceptors.response)
                // throw error прокидывает ошибки (как бы на верхний уровень)
                switch(e.response.data.status) {
                    case 'expired':
                        console.log(' expired [E1] (BOTH TOKENS IS EXPIRED) ');
                        // this.tokenRefresh()
                        break
                    case 'failed':
                        console.log(' failed [E2]');
                        // this.pushNotify('Alarm! Передан некорректный токен.<br>Доступ закрыт', 'notifyRed')
                        break
                    case 'denied':
                        console.log(' denied [E3]');
                        // this.pushNotify('Alarm! Передан пустой токен.<br>Доступ закрыт', 'notifyRed')
                        break
                    case 'approved':
                        console.log(' approved [E4] ');
                        // this.pushNotify('Alarm! Передан пустой токен.<br>Доступ закрыт', 'notifyRed')
                        break
                    case 'locked':
                        console.log(' locked [E5] ');
                        // this.pushNotify('Alarm! Передан пустой токен.<br>Доступ закрыт', 'notifyRed')
                        break
                    default:
                        // this.pushNotify('Ошибка. Подробности в консоли', 'notifyOrange')
                        // console.log(e)
                        console.log('error [E6]: ' + e)
                        break
                } // switch
// ~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~

            } // try
        }, // deleteProduct

    },// methods

    mounted() {
        // this.getItem()
        this.getUpdProd(this.$route.params.id)
        this.item = this.$store.state.updProd
    },

    watch: {
        // '$store.state.products': function() {
        //     console.log('[ProdInfo] -> locale changes detected - getItem()')
            
        //     this.getItem()
        // },
        '$store.state.language': function() {
            console.log('[ProdInfo] -> language changes detected ')
            this.getUpdProd(this.$route.params.id)
            this.item = this.$store.state.updProd
        },
    },// watch

    // Composition API
    setup() {
        const { tokenRefresh } = useAuth()
        const { lang } = useLang()
        const { pushNotify } = useNotify()
        const { deleteData, getUpdProd } = useProducts()

        // return { define, tokenRefresh, pushNotify, deleteData }
        return { define, tokenRefresh, lang, insert, pushNotify, deleteData, getUpdProd }
    }
}
</script>

